<template>
  <div class="floor-detail">
    <!-- 楼层动画 -->
    <div class="floor-overlay-list">
      <!-- <el-button type="primary" @click="toPrev">上一层</el-button>
      <el-button type="primary" @click="toNext">下一层</el-button> -->
      <FloorOverlayItem 
        v-for="(item, index) in floors"
        :key="index"
        :index="index"
        :isShowStatus="curFloor - 1 === index"
        :isShowLine="curFloor - 1 === index" />
    </div>

    <!-- 线条与灯光动画 -->
    <div class="line-and-light" v-show="isShow">
      <canvas class="connect-line-canvas" :width="w" :height="h"></canvas>
      <img class="floor-light" :src="require('@/assets/floor-light.png')" alt="楼层灯光" />
      <div class="cur-floor-num-tips">
        {{ curFloor + '层' }}
        <div
          class="status-color status-line-animation"
          v-if="list.length"
          :style="{
            background: statusColors[list[curFloor - 1].status]
          }"
          >
        </div>
      </div>  
    </div>
    
  </div>
</template>

<script>
import { FloorOverlayItem } from '@/components'
import { delayHandle } from '@/util'

export default {
  components: {
    FloorOverlayItem
  },
  props: {
    status: {
      type: Number,
      default: 1
    },
   curFloor: {
      type: Number,
      default: 1
    }, 
    floors: {
      type: Number,
      default: 25
    },
    list: {
      type: Array,
      default: () => []
    },
    delay: {
      type: Number,
      default: 250
    },
    lineDuration: {
      type: Number,
      default: 350
    },
    statusColors: {
      type: Array,
      deault: () => []
    }
  },
  data() {
    return {
      w: 200,
      h: 300,
      ctx: null,
      isShow: false,
      curFloorNum: 1,
      lastFloorNum: '',
      direction: 'toPrev',
    }
  },
  async mounted() {
    
  },
  methods: {
    async init(time) {
      await delayHandle(time)

      this.isShow = true
      this.initLine()

      await this.animateLine()
      this.floorNumTips(this.lineDuration * 0.8)
      await delayHandle(600)

      this.setCurFloor(this.direction, this.curFloorNum - 1)
    },
    initLine() {
      let canvas = this.$el.querySelector('.connect-line-canvas')
      this.ctx = canvas.getContext('2d')
      this.handleDraw(this.curFloor, this.floors)
    },
    async animateLine() {
      return new Promise(resolve => {
        let el = this.$el.querySelector('.line-and-light')
        el.classList.remove('width-627')
        el.classList.add('width-0')

        setTimeout(() => {
          el.classList.add('width-627')
          resolve()
        }, this.lineDuration)
        // el.classList.add('transition-width')
        // el.classList.add('width-627')

        // el.style.width = 0
        // setTimeout(function () {
        //   Velocity(
        //     el,
        //     { width: '627px' },
        //     {
        //       //  duration: 250,
        //       complete: true
        //     }
        //   )
        //   resolve()
        // }, this.lineDuration)
      })
    },
    handleDraw(curFloor, floors) {
      let precent = 1 - curFloor / floors

      let y = this.h * precent + 2

      this.drawLine([0, y])
    },
    drawLine(startPoint = [], endPoint = [200, 138]) {
      let ctx = this.ctx
      ctx.clearRect(0, 0, 200, 300)
      ctx.lineWidth = 2
      
      ctx.beginPath()
      ctx.moveTo(...startPoint)
      ctx.lineTo(...endPoint)
      ctx.strokeStyle = '#fff'
      ctx.stroke()
    },
    //  楼层指示添动画
    floorNumTips(delay) {
      let el = this.$el.querySelector('.cur-floor-num-tips')
      el.classList.remove('scale-1')
      el.classList.add('scale-0')
      
      setTimeout(() => {
        el.classList.add('scale-1')
      }, delay)
    },
    toNext() {
      this.direction = 'toNext'
      this.curFloorNum--

      //  到达第一层
      if (this.curFloorNum < 1) {
        //  this.curFloorNum = 0
        //  this.curFloorNum = this.floors
        this.curFloorNum = 1
        console.log('到底了')
      }

      this.setCurFloor(this.direction, this.curFloorNum - 1)
    },
    toPrev() {
      this.direction = 'toPrev'
      this.curFloorNum++

      if (this.curFloorNum > this.floors) {
        this.curFloorNum = this.floors

        console.log('到顶了')
      }

      this.setCurFloor(this.direction, this.curFloorNum - 1)
    },
    setCurFloor(direction, cur) {
      //  console.log('setCurFloor direction', direction)
      
      let list = this.$el.querySelectorAll('.floor-overlay-item')
      this.clearFloorItemClassList()

      //  floor-overlay-item-def-top

      if (direction === 'toPrev') {
        list[cur].classList.add('floor-overlay-item-def-top')
        if (cur === 0) {
          list[cur].classList.add('to-prev-1')
        }
        else {
          let last = cur - 1
          list[last].classList.add('pos-prev-1', 'to-prev-3')
          list[cur].classList.add('to-prev-2')
        }
      }
      else {
        console.log('setCurFloor direction', direction)
        if (cur === 0) {
          let top = cur + 1
          list[top].classList.add('pos-prev-1', 'to-next-1')
          list[cur].classList.add('pos-next-1', 'to-next-2')
        }
        else {
          let top = cur + 1
          let bot = cur - 1

          list[top].classList.add('pos-prev-1', 'to-next-1')
          list[cur].classList.add('pos-next-1', 'to-next-2')
          list[bot].classList.add('pos-next-2', 'to-next-4')
        }
      }

      console.log('当前楼层', cur + 1)
      

      this.lastFloorNum = cur
    },
    clearFloorItemClassList() {
      let list = this.$el.querySelectorAll('.floor-overlay-item')

      list.forEach(item => {
        item.classList = ['floor-overlay-item']
      })
    },
    changeFloorNum(floorNum) {
      if (floorNum === this.curFloorNum || floorNum < 1) return

      let val = floorNum - this.curFloorNum
      this.direction = val > 0 ? 'toPrev' : 'toNext'

      this.curFloorNum = floorNum

      this.setCurFloor(this.direction, floorNum - 1)
    }
  },
  watch: {
    // curFloor: {
    //   handler: function(newFloor) {
    //     //  console.log('watch curFloor', newFloor)
    //     this.$nextTick(async () => {
    //       this.handleDraw(newFloor, this.floors)
    //       await this.animateLine()
    //       await delayHandle(600)
    //       this.changeFloorNum(newFloor)
    //     })
    //   },
    //   immediate: true
    // }
    curFloor(newFloor) {
      console.log('watch curFloor', newFloor)
      this.$nextTick(async () => {
        this.handleDraw(newFloor, this.floors)
        this.animateLine()
        this.floorNumTips(this.lineDuration * 0.8)
        await delayHandle(600)
        this.changeFloorNum(newFloor)
      })
    }
  }
}
</script>

