  $top: 180;

  .floor-detail {
    position: absolute;
    top: 270px;
    right: 52px;
    width: 762px;
    height: 462px;
    

    .connect-line-canvas {
      // border: 1px solid red;
      position: absolute;
      top: 0;
      left: 0;
    }

    .floor-overlay-list {
      width: 100%;
      height: 100%;
      position: relative;
    }

    .floor-light {
      width: 427px;
      height: 377px;
      position: absolute;
      top: -45px;
      left: 197px;
    }

    .line-and-light {
      width: 627px;
      height: 377px;
      position: absolute;
      top: 35px;
      left: -154px;
      display: flex;
      overflow: hidden;
      // background: red;
    }

    .cur-floor-num-tips {
      width: 32px;
      height: 48px;
      line-height: 35px;
      text-align: center;
      background: url('../../../assets/cur-floor-num-bg.png') no-repeat;
      background-size: contain;
      position: absolute;
      top: 62px;
      left: 194px;
      font-size: 12px;
      // font-family: NotoSansHans Chinese Bold;
      font-weight: bold;
      color: #EBF6FF;
      overflow: hidden;
      transform-origin: left bottom;

      .status-color {
        width: 32px;
        height: 4px;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .scale-0 {
      transform: scale(0);
    }
    .scale-1 {
      transform: scale(1);
      transition: all .3s linear;
    }
    .width-0 {
      width: 0;
    }
    .width-627 {
      width: 627px;
      transition: width .5s linear;
    }
    .transition-width {
      transition: width .35s ease 1s;
    }


    .floor-overlay-item {
      position: absolute;
      // top: 27px;
      right: 41px;
      width: 517px;
      height: 305px;
      opacity: 0;
      transform-origin: center center;
    }

    .floor-overlay-item-def-top {
      top: 27px;
    }

    .to-prev-1 {
      transform: translateY(20px);
      opacity: 1;
      transition: all .4s;
      // z-index: 2;
    }
    .pos-prev-1 {
      top: 47px;
      opacity: 1;
    }

    .to-prev-2 {
      transform: translateY(20px);
      opacity: 1;
      transition: all .4s ease .2s;
    }
    .to-prev-3 {
      transform: translateY(153px) scale(0.8);
      opacity: 0.5;
      transition: all .25s;
    }

    .pos-next-1 {
      top: $top + px;
      transform: scale(0.8);
      opacity: 0.5;
    }
    .to-next-1 {
      transform: translateY(-20px);
      opacity: 0;
      transition: all .2s ease;
    }
    .to-next-2 {
      transform: translateY(-133px) scale(1);
      opacity: 1;
      transition: all .25s ease .2s;
    }
    .to-next-3 {
      transform: scale(1);
      opacity: 1;
      transition: all .25s ease .2s;
    }
    .pos-next-2 {
      top: $top + px;
      transform: scale(0.8);
      opacity: 0;
    }
    .to-next-4{
      opacity: 0.5;
      transform: scale(0.8);
      transition: opacity .4s ease .3s;
    }
  }
  .status-line-animation {
    -webkit-animation: 2.2s status-line-translate ease infinite;
    animation: 2.2s status-line-translate ease infinite;
  }
  @-webkit-keyframes status-line-translate {
    0% {
      width: 0;
    }
    35% {
      width: 100%;
    }
    50% {
      width: 100%;
      transform: translateX(0%);
    }
    70%, 100% {
      transform: translateX(100%);
    }
  }
  @keyframes status-line-translate {
    0% {
      width: 0;
    }
    35% {
      width: 100%;
    }
    50% {
      width: 100%;
      transform: translateX(0%);
    }
    70%, 100% {
      transform: translateX(100%);
    }
  }
</style>