<template>
<PageBg>
  <div class="page" :style="{transform: 'scale(' + scale + ') translate(-50%, -50%)'}">
    <PageHeader :title="title" :isFullScreen="isFullScreen" :switchFullScreen="handleSwitchFullSceen" />

    <div class="page-content bsizing">
      <div class="layout-l">
        <DeviceStatus ref="deviceStatus" :chartData="pieChartData" :offline="offline" :online="online" />
        <AlarmPoint />
        <RealTimeAlarm />
      </div>

      <div class="layout-r bsizing">
        <div class="park">
          <img class="bg" :src="require('@/assets/park.png')" alt="bg" />

          <!-- 热力条组件 -->
          <GradientBar :pageScale="scale" />

          <!-- 大厦楼层组件 -->
          <FloorBuilding
            ref="floorBuilding"
            :list="floorList"
            :floors="floors"
            :curFloor="curFloor"
            :delay="buildingAnimateDelay" />
          
          <!-- 楼层叠加动画组件 -->
          <FloorDetail
            ref="floorDetail"
            :status="2"
            :floors="floors"
            :curFloor="curFloor"
            :list="floorList"
            :delay="buildingAnimateDelay"
            :lineDuration="lineDuration"
            :statusColors="statusColors" />
        </div>

        <div class="view-data bsizing">
          <AlarmStatistics />
          <AllMonitor
            :totalFloors="floors"
            :list="floorList"
            :curFloorChange="curFloorChange" />
        </div>

        <FloorStatistics />
      </div>
    </div>
  </div>
</PageBg>
</template>

<script>
import { PageHeader, PageBg, GradientBar } from '@/components'
import DeviceStatus from './components/DeviceStatus'
import AlarmPoint from './components/AlarmPoint'
import RealTimeAlarm from './components/RealTimeAlarm'
import AlarmStatistics from './components/AlarmStatistics'
import AllMonitor from './components/AllMonitor'
import FloorStatistics from './components/FloorStatistics'
import FloorBuilding from './components/FloorBuilding'
import FloorDetail from './components/FloorDetail'

import {
  fullScreen,
  cancelFullScreen,
  addHandle,
  removeHandle,
  debounce,
  getBrowserPrefix,
  delayHandle
} from '@/util'

export default {
  components: {
    PageHeader,
    DeviceStatus,
    AlarmPoint,
    RealTimeAlarm,
    AlarmStatistics,
    PageBg,
    AllMonitor,
    FloorStatistics,
    GradientBar,
    FloorBuilding,
    FloorDetail
  },
  data() {
    return {
      scale: 1,
      title: '山东产业技术研究院楼宇人员热力分布',
      isFullScreen: false,
      statusColors: ['#CFDCE6', '#E6312E', '#E6722E', '#B5CF34', '#7EE639', '#45E66F', '#2EDAE6'],
      floorList: [],
      floors: 0,       //  总楼层数
      curFloor: 1,     //  当前停留楼层数
      delay: 4000,
      buildingAnimateDelay: 100,   //  每层楼层动画时间
      lineDuration: 350,
      interTimer: null,
      direction: 'toPrev',
      pieChartData: [],
      online: '',
      offline: '',
    }
  },
  created() {
    this.handleResize()
  },
  async mounted() {
    let debounceCb = debounce.bind(this)(this.handleResize, 200)
    addHandle(window, 'resize', debounceCb)

    //  浏览器全屏change事件
    let bowserPrefix = getBrowserPrefix()
    addHandle(window, bowserPrefix + 'fullscreenchange', this.fullscreenchange.bind(this))

    await this.checkFloorList(25)
    
    //  楼层轮播动画
    let time = this.floors * this.buildingAnimateDelay
    
    this.$nextTick(() => {
      //  楼层叠加动画
      delayHandle(time)
      .then(() => {
        //  楼层详情初始化
        this.$refs.floorDetail.init()
        //  楼层详情轮播动画
        this.intervalChangeCurFloor()
      })
    })

    this.checkDeviceStatus()
  },
  methods: {
    //  查询楼层数
    async checkFloorList(num) {
      await delayHandle(0)

      let arr = []
      let statusLen = this.statusColors.length

      for(let i = 0; i < num; i++) {
        let obj = {
          status: i % statusLen + 1,
          id: i + 1,
          num: i + 1 + '人',
          isOnline: i % 2,
          name: i + 1 + '层',
          pic: i % 4 + 1
        }

        arr.push(obj)
      }

      
      this.floorList = arr
      this.floors = arr.length

      //  console.log('checkFloorList', this.floorList)
    },
    //  查询设备状态
    async checkDeviceStatus() {
      await delayHandle(2000)
      let data = [
        { name: '在线', value: 27 },
        { name: '离线', value: 25 }
      ]

      this.pieChartData = data

      this.offline = data[1].value
      this.online = data[0].value

      this.$refs.deviceStatus.initChart(data)
    },
    fullscreenchange() {
      console.log('full screen change')
      let isFullScreen = document.webkitIsFullScreen || document.mozFullScreen || document.fullScreen
      //  console.log(isFullScreen)
      if (!isFullScreen) {
        //  esc键退出全屏
        this.isFullScreen = false
      }
      else {
        this.isFullScreen = true
      }
    },
    handleResize() {
      console.log('handleResize')
      let scale = 0
      let defWidth = 1920
      let defHeight = 1080
      let defRatioVal = 16 / 9
      let clientEl = document.documentElement
      let clientWidth = clientEl.clientWidth
      let clientHeight = clientEl.clientHeight
      let curRatioVal = clientWidth / clientHeight

      if (curRatioVal > defRatioVal) {
        scale = clientHeight / defHeight
      }
      else {
        scale = clientWidth / defWidth
      }

      this.scale = scale
    },
    handleSwitchFullSceen() {
      console.log('handleSwitchFullSceen')
      this.isFullScreen = !this.isFullScreen

      this.isFullScreen ? fullScreen() : cancelFullScreen()
    },
    //  楼层切换轮播
    intervalChangeCurFloor() {
      let delay = this.delay

      this.interTimer = setInterval(() => {
        if (this.curFloor >= this.floors) {
          this.direction = 'toNext'
        }
        
        if (this.curFloor <= 1) {
          this.direction = 'toPrev'
        }

        if (this.direction === 'toPrev') {
          this.curFloor++
        }
        else {
          this.curFloor--
        }

        //  console.log('intervalChangeCurFloor curFloor', this.curFloor)
      }, delay)
    },
    //  清除轮播
    clearInterChangeFloor() {
      let interTimer = this.interTimer
      interTimer && clearInterval(interTimer)
      
      interTimer = null
    },
    //  楼层切换模块  楼层改变
    curFloorChange(curFloor) {
      this.clearInterChangeFloor()
      this.curFloor = curFloor

      this.intervalChangeCurFloor()
    }
  },
  beforeDestroy() {
    removeHandle(window, 'resize', this.handleResize)

    let bowserPrefix = getBrowserPrefix()
    removeHandle(window, bowserPrefix + 'fullscreenchange', this.fullscreenchange)

    this.clearInterChangeFloor()
  },
  
}
</script>

