<template>
  <div class="all-monitor">
    <TextTitle title="所有监控" />
    <BorderBox1
      :w="578"
      :h="264"
      :handleMouseenter="handleMouseenter"
      :handleMouseleave="handleDebounce(this.handleMouseleave, 100)">
      <div class="left">
        <TextTitle2 title="楼层概览" />

        <MonitorList
          :activeIndex="activeIndex"
          :list="curFloors"
          :pageSize="floorPageSize"
          :page="floorPage"
          :pageTotal="floorPageTotal"
          :delay="delay"
          :currentChange="handlePageClick"
          :handleSelectFloor="handleSelectFloor"
           />
      </div>
      <div class="right">
        <TextTitle2 title="当前楼层画面" />
        <transition name="fade" mode="out-in">
          <div
            class="video-container"
            v-if="isShowPic && curFloors.length">
            <img
              :src="require('@/assets/snap-shot-0'+ curFloors[activeIndex].pic +'.png')" alt="">
          </div>
        </transition>
      </div>
    </BorderBox1>
  </div>
</template>

<script>
import { TextTitle, BorderBox1, TextTitle2, MonitorList } from '@/components'
import { debounce } from '@/util'

export default {
  props: {
    totalFloors: {
      type: Number,
      default: 25
    },
    list: {
      type: Array,
      default: () => []
    },
    //  选择楼层
    curFloorChange: {
      type: Function,
      default: curFloor => {}
    }
  },
  components: {
    TextTitle,
    BorderBox1,
    TextTitle2,
    MonitorList
  },
  data() {
    return {
      floors: [],
      curFloors: [],
      floorPageSize: 6,
      floorPage: 1,
      floorPageTotal: 0,
      delay: 250,
      timer: null,
      playTime: 3000,
      activeIndex: 0,
      hasInitPlay: false,
      isShowPic: true
    }
  },
  mounted() {
    
  },
  methods: {
    updateFloors(arr) {
      let floorPageSize = this.floorPageSize
      this.floorPageTotal = Math.ceil(arr.length / floorPageSize)
      this.floors = arr
      this.curFloors = arr.slice(floorPageSize * (this.floorPage - 1), floorPageSize * this.floorPage)
    },
    //  初始化每页动画结束播放
    initPagePlay() {
      this.hasInitPlay = true
      this.clearAutoPlay()

      let curLen = this.curFloors.length
      let queryTime = this.delay * curLen

      //  console.log('initPagePlay curFloors', this.curFloors)

      if (!curLen) return

      setTimeout(() => {
        this.autoPlayEveryFloorMonitor()
      }, queryTime)
    },
    //  自动播放每层楼视频
    autoPlayEveryFloorMonitor() {
      let curLen = this.curFloors.length
      let playTime = this.playTime

      this.timer = setInterval(() => {
        let arr = this.floors
        let floorPageSize = this.floorPageSize
        this.activeIndex++

        this.isShowPic = false

        setTimeout(() => {
          this.isShowPic = true
        }, 20)

        //  console.log('autoPlayEveryFloorMonitor', this.activeIndex)

        if (this.activeIndex >= curLen) {
          this.activeIndex = 0

          this.floorPage++
          this.floorPage = this.floorPage > this.floorPageTotal ? 1 : this.floorPage
          this.curFloors = []

          this.curFloors = arr.slice(floorPageSize * (this.floorPage - 1), floorPageSize * this.floorPage)

          this.initPagePlay()
        }
      }, playTime)
    },
    //  清空自动播放
    clearAutoPlay() {
      let timer = this.timer

      timer && clearInterval(timer)
      timer = null
    },
    //  鼠标移入容器  暂停轮播
    handleMouseenter() {
      console.log('handleMouseenter')
      this.clearAutoPlay()
    },
    //  鼠标移出容器  重启轮播
    handleMouseleave() {
      console.log('handleMouseleave')
      this.autoPlayEveryFloorMonitor()
    },
    //  分页点击
    handlePageClick(page) {
      let arr = this.floors
      let floorPageSize = this.floorPageSize
      this.activeIndex = 0

      console.log('handlePageClick page', page)
      this.floorPage = page
      this.curFloors = arr.slice(floorPageSize * (this.floorPage - 1), floorPageSize * this.floorPage)
    },
    //  选择楼层
    handleSelectFloor(curFloorIndex) {
      this.activeIndex = curFloorIndex

      let curFloor = this.floorPageSize * (this.floorPage - 1) + curFloorIndex + 1

      this.curFloorChange(curFloor)
    },
    handleDebounce: debounce
  },
  beforeDestroy() {
    this.clearAutoPlay()
  },
  watch: {
    list(newList) {
      this.updateFloors(newList)

      if (this.hasInitPlay) return

      this.$nextTick(() => {
        this.initPagePlay()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .all-monitor {
    margin-left: 24px;
    .border-box-1 {
      margin-top: 12px;
      padding: 16px 24px;
      display: flex;

      .left {
        flex: 1;
        height: 100%;


      }

      .right {
        width: 326px;
        height: 100%;

        .video-container {
          margin-top: 16px;
          width: 100%;
          height: 184px;
          
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
</style>